import React,{Component}from 'react';
import {
  BrowserRouter as Router,
  Link,
  Switch,
  Route,
  useParams
} from "react-router-dom";

const Muhuratlist=(props)=>{
	  const data =props.data;
	
		return(
				<div className="col-md-9">
					<h3>Choose Your Best Muhurat</h3>
					<div className="Muhuratlist-react-se">
					{ data.length===0 &&
						
						<img src="data:image/gif;base64," />
					
						}
						{	data.length > 1 && 
							<ul>
							 {data.map((rowData, index) => (
								<li key={index+1}>
									<Link  to={'/muhurat/'+rowData.muhurat_slug.toLowerCase()}  >
									<div className="comm-img">
										<img src={ require(`../../assets/muhurat/${rowData.muhurat_image}`) } alt={rowData.title} />
									</div>
									<p>{rowData.title}</p>
									</Link>
								</li>
								))};
							</ul>
						}
					</div>
				</div>
		
		);

}
export default Muhuratlist;