import React,{Component}from 'react';
import {
  BrowserRouter as Router,
  Link,
  Switch,
  Route,
  useParams
} from "react-router-dom";
import * as ROUTES from '../../config/routes';

const Vastulist =(props)=>{
		const data=props.data;
		return(
				<div className="col-md-9">
					<h3>Choose Your Best Vastu</h3>
					<div className="Muhuratlist-react-se">
				 	{ data.length===0 &&
						
						<img src="data:image/gif;base64," />
					
						}
						{data.length > 1 && 
						<ul>
						 {data.map((rowData, index) => (
							<li key={index+1}> 
						  	  <Link  to={'/micro-vastu/'+rowData.vastu_slug.toLowerCase()}  >
								<div className="comm-img">
									<img  src={ require(`../../assets/vastu/${rowData.vastu_image}`) } alt={rowData.title} />
								</div>
								<p>{rowData.title}</p>
								</Link>
							</li>
							))};
						</ul>
						}
					</div>
				</div>
		
		);
 }
export default Vastulist;